<template>
  <div class="keywordTab">
    <div class="tabContent"
         v-if="!modalVisible">
      <div class="tabTop">
        <a-button type="primary"
                  @click="addKeyWord">
          <a-icon type="plus-circle" /> 添加
        </a-button>
      </div>
      <!-- <a-table :columns="columns"
             :dataSource="data"
             bordered>
      <template slot="name"
                slot-scope="text">
        <a style="color:#f00">{{ text }}</a>
      </template>
    </a-table> -->

      <!-- 列表 -->
      <div class="tabWrap">
        <table class="kwTable">
          <tbody>
            <tr>
              <th>关键词名</th>
              <th>关键词组</th>
              <th width="100">操作</th>
            </tr>
            <tr v-for="(item,index) in tableData"
                :key="index">
              <td width="150">{{item.key_name}}</td>
              <td class="pd0">
                <div class="keylistBox">
                  <div class="keylist"
                       v-for="(childItem,childIndex) in item.key_words_list"
                       :key="childIndex">
                    <span>{{childItem.key}}</span>
                    <div class="tags">
                      <a v-for="(thirdItem,thirdIndex) in childItem.words_list"
                         :key="thirdIndex">{{thirdItem}}
                        <i title="删除">x</i>
                      </a>
                    </div>
                  </div>
                </div>
              </td>
              <td>
                <a @click="detailClick(item.id)">详情</a>
                <a @click="delItemClick(item.id)">删除</a>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="pages">
          <a-pagination v-model="current"
                        @change="onChange"
                        :pageSize="pageSize"
                        :total="total" />
        </div>
      </div>
    </div>
    <!-- 详情 -->
    <Detail v-if="modalVisible"
            :detaildata="detaildata"
            v-on:backClickfn="backClickfn"
            @detailDataFn="detailDataFn"></Detail>

    <!--添加弹出层-->
    <a-modal title="添加"
             centered
             v-model="addflag"
             @ok="sureClick">
      <a-form-item label="关键词名"
                   :label-col="{ span: 8 }"
                   :wrapper-col="{ span: 10 }">
        <a-input v-model="addKeyWordName" />
      </a-form-item>
    </a-modal>
  </div>
</template>

<script>
import $axios from '../../../../utils/request.js'
import Detail from './Detail'
export default {
  name: 'KeyWord',
  data () {
    return {
      tableData: [],
      current: 1, // 当前页
      limit: 5, // 每页条数
      pageSize: 5, // 每页条数
      page: 1, // 第一页
      total: 0, // 数据总条数
      modalVisible: false,
      detaildata: {},
      addflag: false,
      addKeyWordName: ''
    }
  },
  components: {
    Detail
  },
  mounted () {
    this.keyWordFn()
  },
  methods: {
    keyWordFn () {
      let url = `${this.baseURL}/key_words/select?page=${this.page}&limit=${this.limit}`
      console.log($axios)
      this.$http.get(url).then(res => {
        if (res.status === 200) {
          this.total = res.data.count
          this.tableData = res.data.data
        }
      })
    },
    onChange (current) { // 页码改变
      this.current = current
      this.page = current
      this.keyWordFn()
    },
    detailClick (id) { // 详情点击
      console.log(id)
      this.modalVisible = true
      this.detailDataFn(id)
    },
    detailDataFn (id) { // 详情数据获取
      let url = `${this.baseURL}/key_words/select_by_id?id=${id}`
      this.$http.get(url).then(res => {
        if (res.status === 200) {
          this.detaildata = res.data.data
          console.log(this.detaildata)
        }
      })
    },
    f () {
      console.log('father')
    },
    delItemClick (id) {
      let url = `${this.baseURL}/key_words/delete_by_id?id=${id}`
      this.$http.get(url).then(res => {
        if (res.status === 200 && res.data.code === '400') {
          this.$error({ // 关键词中有词组不能删除，弹出提示框
            title: '提示',
            content: res.data.message
          })
        }
      })
    },
    backClickfn () {
      this.modalVisible = false
    },
    addKeyWord () {
      this.addflag = true
    },
    sureClick () {
      let url = `${this.baseURL}/key_words/add_key_word?key_name=${this.addKeyWordName}`
      this.$http.get(url).then(res => {
        if (res.status === 200) {
          console.log(res)
          this.addKeyWordName = ''
          this.keyWordFn()
          this.addflag = false
        }
      })
      console.log(this.addKeyWordName)
    }
  }
}
</script>
<style scoped>
.tabTop {
  padding: 20px;
  text-align: right;
}
.tabWrap {
  padding: 0 16px;
}
.kwTable {
  width: 100%;
}
.kwTable th {
  padding: 16px 16px;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
  text-align: left;
  background: #fafafa;
  border: 1px solid #e8e8e8;
}
.kwTable td {
  padding: 0 16px;
  line-height: 35px;
  border: 1px solid #e8e8e8;
}
.kwTable td.pd0 {
  padding: 0;
}
.keylist {
  display: flex;
  position: relative;
}
.keylist + .keylist {
  border-top: 1px solid #e8e8e8;
}
.keylist span {
  border-right: 1px solid #e8e8e8;
  padding: 0 16px;
  line-height: 46px;
}
.tags {
  display: inline-block;
  padding: 5px 16px;
}
.tags a {
  margin-right: 15px;
  background: #72bcff;
  border: 1px solid #1890ff;
  padding: 4px 6px;
  font-size: 12px;
  border-radius: 3px;
  color: #fff;
  position: relative;
}
.tags a i {
  display: inline-block;
  width: 15px;
  height: 15px;
  text-align: center;
  background: #f00;
  color: #fff;
  border-radius: 50%;
  line-height: 15px;
  font-size: 12px;
  position: absolute;
  right: -9px;
  top: -8px;
  z-index: 2;
  display: none;
}
.tags a:hover i {
  display: block;
}
.operate {
  position: absolute;
  right: 16px;
  top: 0;
  bottom: 0;
  border-left: 1px solid #e8e8e8;
  padding-left: 16px;
  line-height: 48px;
}
.pages {
  text-align: right;
  margin: 16px 0;
}
</style>



