<template>
  <div>
    <div class="f-mask"></div>
    <div class="inner">
      <div class="header">
        <h3>{{itemData.key}}</h3>
        <span class="close"
              @click="cancelClick(detaildata.id)">
          <a-icon type="close" />
        </span>
      </div>
      <div class="edit-content">
        <div class="addbox">
          <input type="text"
                 v-model="keywordtxt">
          <a-button class="add"
                    type="primary"
                    @click="subkeyadd(itemData.word_list)">
            <a-icon type="plus" />添加
          </a-button>
        </div>
        <p>
          <em v-for="(item,index) in tempList"
              v-if="item!=''"
              :key="index">
            {{item}}
            <b class="sdelbtn"
               title="删除"
               @click="delClick(itemData.id,itemData.word_list,index)">
              x
            </b>
          </em>
        </p>
      </div>
      <div class="btns">
        <a class="u-button"
           @click="cancelClick(detaildata.id)">取消</a>
        <a class="u-button f-cblue"
           @click="sureClick(itemData.id,detaildata.id,itemData.word_list)">确定</a>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: 'EditPop',
  props: {
    itemData: {
      type: Object,
      default () {
        return {}
      }
    },
    detaildata: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      keywordtxt: '', // input框中输入的关键词组名
      keywordlist: [],
      tempList: this.itemData.word_list
    }
  },
  methods: {
    cancelClick (id) { // 取消
      this.$emit('itemCancel', id)
    },
    sureClick (id, did, arr) { // 确定
      this.submitFn(id, arr)
      this.$emit('sureitemCancelClick', did)
    },
    delClick (id, wordlist, index) {
      if (this.keywordlist.length === 0) {
        this.keywordlist = JSON.parse(JSON.stringify(wordlist))// 深拷贝
      }
      this.keywordlist.splice(index, 1)
      this.tempList = this.keywordlist
    },
    subkeyadd (listArr) { // 添加
      if (this.keywordlist.length === 0) {
        this.keywordlist = JSON.parse(JSON.stringify(listArr))// 深拷贝
      }
      this.keywordlist.push(this.keywordtxt)
      this.tempList = this.keywordlist
      this.keywordtxt = ''
    },
    submitFn (id, listArr) {
      if (this.keywordlist.length === 0) {
        this.keywordlist = listArr // 为了防止直接点击确定的是，keywordlist是空值，要把原有存在的数据赋值进去
      }
      let _this = this
      let url = `${this.baseURL}/word_list/update_by_id?id=${id}&word_list=${this.keywordlist}`
      this.$http.get(url).then(res => {
        if (res.status === 200) {
          console.log(this.res)
          console.log(_this.detaildata.id)
          _this.$parent.$parent.detailDataFn(_this.detaildata.id)// 刷新祖父组件详情数据
        }
      })
    }
  }
}

</script>
<style scoped>
.header {
  padding: 16px 24px;
  border-bottom: 1px solid #e8e8e8;
  position: relative;
}
.header .close {
  width: 56px;
  height: 56px;
  line-height: 56px;
  text-align: center;
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
}
.header h3 {
  color: rgba(0, 0, 0, 0.85);
  font-weight: bold;
  font-size: 16px;
  line-height: 22px;
}
.edit-content {
  padding: 24px;
}
.edit-content input {
  height: 32px;
  padding: 4px 11px;
  color: rgba(0, 0, 0, 0.65);
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  transition: all 0.3s;
}
.edit-content .addbox {
  margin-bottom: 15px;
}
.edit-content .add {
  font-size: 12px;
  padding: 0 6px;
}
.edit-content .add i {
  vertical-align: -2px;
  margin-right: -6px;
}
.edit-content em {
  display: inline-block;
  margin-right: 15px;
  margin-bottom: 10px;
  background: #72bcff;
  border: 1px solid #1890ff;
  padding: 4px 6px;
  font-size: 12px;
  border-radius: 3px;
  color: #fff;
  position: relative;
  cursor: pointer;
}
.edit-content em .sdelbtn {
  background: #f00;
  position: absolute;
  top: -7px;
  right: -7px;
  width: 15px;
  height: 15px;
  text-align: center;
  font-size: 12px;
  line-height: 15px;
  transition: right 0.3s ease-out;
  border-radius: 50%;
}
.btns {
  padding: 10px 16px;
  text-align: right;
  background: transparent;
  border-top: 1px solid #e8e8e8;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}
.btns .u-button {
  line-height: 30px;
  position: relative;
  display: inline-block;
  font-weight: 400;
  text-align: center;
  background-image: none;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.015);
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  height: 32px;
  padding: 0 15px;
  font-size: 14px;
  border-radius: 4px;
  color: rgba(0, 0, 0, 0.65);
  background-color: #fff;
  border: 1px solid #d9d9d9;
}
.btns .u-button.f-cblue {
  color: #fff;
  background-color: #1890ff;
  border-color: #1890ff;
  margin-left: 8px;
}
.f-mask {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.45);
  filter: alpha(opacity=50);
}
.inner {
  min-width: 500px;
  max-width: 600px;
  min-height: 350px;
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 9999;
  background: #fff;
  border-radius: 4px;
  transform: translate(-50%, -50%);
}
</style>



