<template>
  <div class="detail-cont">
    <div class="tabTop">
      <a-button type="primary"
                @click="backClick">
        <a-icon type="arrow-left" /> 返回
      </a-button>
    </div>
    <div class="detailPop">
      <div class="keyname">
        <h3>关键词名：</h3>
        <a>{{detaildata.key_name}}</a>
      </div>
      <h4>关键词组：</h4>
      <div class="addgroup">
        <input type="text"
               v-model="keygroupval">
        <a-button class="add"
                  type="primary"
                  @click="groupAdd(detaildata.pid,detaildata.id)">
          <a-icon type="plus" />添加关键词组
        </a-button>
      </div>
      <ul class="groupname">
        <li v-for="(item,index) in detaildata.word_list"
            :key="index">
          <span>{{item.key}}：
            <a-icon type="edit"
                    class="edit"
                    title="编辑"
                    @click="editClick(item)" />
            <a-icon type="delete"
                    class="delete"
                    title="删除"
                    @click="delClick(item.id,detaildata.id)" />
          </span>
          <p>
            <em v-for="(childItem,childIndex) in item.word_list"
                v-if="childItem!=''"
                :key="childIndex">
              {{childItem}}
              <!-- <b class="sdelbtn"
                 title="删除">
                <a-icon type="delete" />
              </b>-->
            </em>
          </p>

          <!-- <div class="addbox">
            <input type="text">
            <a-button class="add"
                      type="primary">
              <a-icon type="plus" />添加
            </a-button>
          </div> -->
        </li>
      </ul>
    </div>

    <!-- 编辑弹出层 -->
    <EditPop v-if="editShow"
             :itemData="itemData"
             :detaildata='detaildata'
             v-on:itemCancel="itemCancelClick"
             v-on:sureitemCancelClick="sureitemCancelClick"></EditPop>
  </div>
</template>

<script>
import EditPop from './EditPop'
export default {
  name: 'Detail',
  props: {
    detaildata: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  components: {
    EditPop
  },
  data () {
    return {
      editShow: false,
      itemData: {},
      keygroupval: ''
    }
  },
  methods: {
    backClick () {
      console.log('bb')
      this.$emit('backClickfn')
    },
    editClick (item) {
      this.editShow = true
      this.itemData = item
      console.log(item)
    },
    delClick (id, listId) { // 删除词组
      console.log(id, listId)
      let _this = this
      this.$confirm({
        title: '删除',
        content: '确定要删除该关键词组吗',
        onOk () {
          console.log('OK')
          _this.delteFn(id, listId)
        },
        onCancel () { }
      })
    },
    delteFn (id, listId) { // 删除接口
      console.log(listId)
      let url = `${this.baseURL}/word_list/delete_word_list_by_id?id=${id}`
      this.$http.get(url).then(res => {
        if (res.status === 200) {
          console.log(this.res)
          this.$parent.detailDataFn(listId)// 刷新详情数据
        }
      })
    },
    itemCancelClick (id) {
      this.editShow = false
      console.log(id)
      this.$parent.detailDataFn(id)
    },
    sureitemCancelClick (id) {
      this.editShow = false
    },
    groupAdd (pid, id) { // 添加关键词组接口
      console.log(id)
      if (this.keygroupval !== '') {
        let url = `${this.baseURL}/word_list/add_word_list_by_key_id?pid=${pid}&key=${this.keygroupval}`
        this.$http.get(url).then(res => {
          if (res.status === 200) {
            console.log(this.res)
            this.$parent.detailDataFn(id)// 刷新详情数据
          }
        })
        this.keygroupval = ''
      }
    }
  }
}

</script>
<style scoped>
.detailPop {
  padding: 0 20px 0 20px;
}
.tabTop {
  padding: 20px;
  text-align: right;
}
.detailPop h4 {
  font-weight: bold;
  font-size: 14px;
  margin-bottom: 15px;
}
.keyname {
  /* border-bottom: 1px solid #e8e8e8; */
  padding-bottom: 20px;
}
.keyname h3 {
  font-weight: bold;
  display: inline-block;
}
.groupname li {
  margin-left: 30px;
  padding-bottom: 30px;
  position: relative;
  border-bottom: 1px solid #e8e8e8;
  margin-bottom: 15px;
}
.groupname li:last-child {
  border: 0;
}
.groupname li span {
  font-weight: bold;
  font-size: 14px;
  margin-bottom: 10px;
  display: block;
  color: #666;
}
.groupname li span .delete {
  color: #f00;
  cursor: pointer;
  margin-left: 10px;
}
.groupname li em {
  display: inline-block;
  margin-right: 15px;
  margin-bottom: 5px;
  background: #72bcff;
  border: 1px solid #1890ff;
  padding: 4px 6px;
  font-size: 12px;
  border-radius: 3px;
  color: #fff;
  position: relative;
  overflow: hidden;
}
.groupname li em .sdelbtn {
  background: #f00;
  position: absolute;
  top: 0;
  bottom: 0;
  right: -100px;
  width: 20px;
  text-align: center;
  font-size: 14px;
  line-height: 20px;
  transition: right 0.3s ease-out;
}
.groupname li em:hover .sdelbtn {
  right: 0;
}
.groupname li .addbox {
  position: absolute;
  bottom: 15px;
}
.detail-cont input {
  height: 32px;
  padding: 4px 11px;
  color: rgba(0, 0, 0, 0.65);
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  transition: all 0.3s;
}
.groupname li .add {
  font-size: 12px;
  padding: 0 6px;
  background-color: #5eb2ff;
}
.groupname li .add i {
  vertical-align: -2px;
  margin-right: -6px;
}
.groupname .edit {
  color: #009867;
  font-size: 16px;
  cursor: pointer;
}
.addgroup {
  background: #f0f0f0;
  margin: 10px 0 30px 30px;
  padding: 10px;
  border-radius: 3px;
}
</style>



